<template>
    <div class="article-wrapper">
        <div class="article">
             <div class="title">
                <label for="title">: </label>
                <input type="text" id="title" v-model="articleTitle">
            </div>
            <div class="tag">
                <label for="tag">: </label>
                <input type="text" id="tag" placeholder="" v-model="articleTag" @keyup.enter="addTag">
                <ul class="tags-wrapper">
                    <li class="has-tag" v-for="(item, index) in tagArr">
                        <span><i class="iconfont icon-tag i-tag"></i>{{ item.name }}</span>
                        <i class="iconfont icon-delete" @click.stop="deleteTag(item._id, index)"></i>
                    </li>
                </ul>
            </div>        
            <textarea id="editor"></textarea>
            <div class="btn">
                <div class="delete" @click.stop="emptyDraft"></div>
                <div class="save" @click.stop="publishArticle"></div>
            </div>
        </div>
    </div>
</template>

<script>
//this.$store.state.currentArticle
//currentArticle.id === -1 
//
//""/admin/article
//"admin/article"
//
//
//
import Simplemde from 'simplemde';
import marked from 'assets/js/marked.js';
//
import { mapState } from 'vuex';
//
let simplemde;

export default {
    data(){
        return {
            articleTitle: '',
            articleTag: '',
            tagArr: [],
            articleContent: ''
        }
    },
    //state.articlecomputedmutations,actions,
    //
    computed: {
        ...mapState({
            currentArticle: state => state.article.currentArticle,
            allArticles: state => state.article.allArticles
        })
    },
    beforeRouteEnter(to, from, next){
        let index = to.query.index;
        if(to.path === "/admin/article" && from.path === "/admin/list" && index >= 0){
            //console.log('');
            next(vm => {
                let title = vm.allArticles[index].title;
                let content = vm.allArticles[index].content;
                let tags = vm.allArticles[index].tags.slice(0);
                vm.articleTitle = title;
                vm.articleContent = content;
                vm.tagArr = tags;
                simplemde.value(content);
                //
                vm.$store.commit('ARTICLE_MODIFY', {
                    article: vm.allArticles[index],
                    index
                });
            });
        }else{
            next(vm => {
                //slice(0)
                //tagArr
                vm.tagArr = vm.currentArticle.tags.slice(0);
            });
        }
    },
    mounted(){
        simplemde = new Simplemde({
            autoDownloadFontAwesome:false,
            spellChecker:false,
            element: document.getElementById('editor'),
            placeholder: "Press Ctrl+S to save the article...",
            previewRender: function(plainText) {
                return marked(plainText); // Returns HTML from a custom parser
            },
            tabSize: 4
        });
        //change
        simplemde.codemirror.on("change", () => {
            let value = simplemde.value();
            //
            if(this.articleContent === value){
                return;
            }
            this.articleContent = value;
        });
        //ctrl+s
        simplemde.codemirror.on('keydown', (cm, e) => {
            if( e.ctrlKey  == true && e.keyCode === 83 ){
                //
                this.saveArticle();
                e.preventDefault();
	        }
        });
    },
    methods: {
        //
        emptyDraft(){
            ////Ctrl+S
            if(this.articleTitle !== '' && this.tagArr.length !== 0 && this.articleContent !== '' && this.currentArticle.save === false){
                this.$message({
                    type: 'warning',
                    message: ''
                });
                return ;
            }
            this.$confirm(',?', '')
                .then(() => {
                    this.articleTitle = '';
                    this.articleTag = '';
                    this.articleContent = '';
                    this.tagArr = [];
                    this.$store.commit('EMPTY_DRAFT');
                    simplemde.value('');
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                })
                .catch(() => {});
        },
        //:
        publishArticle(){
            if(this.articleContent === '' || this.tagArr.length === 0 || this.articleTitle === ''){
                this.$message({
                    type: 'success',
                    message: '!'
                });
                return;
            }
            //save
            if(this.currentArticle.save === false){
                this.$message({
                    type: 'warning',
                    message: '!'
                });
                return ;
            }
            //publish
            if(this.currentArticle.publish === true){
                this.$message({
                    type: 'success',
                    message: ',!'
                });
                return ;
            }
            this.$confirm(',?', '')
                .then(res => {
                    this.$store.dispatch('publishArticle', { id: this.currentArticle._id })
                        .then(res => {
                            if(res){
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                            }
                        })
                        .catch(err => {
                            console.log('!');
                            this.$message({
                                type: 'error',
                                message: '!'
                            });
                        });
                })
                .catch(() => {});
            
        },
        //-----//
        saveArticle(){
            let title = this.articleTitle;
            let content = this.articleContent;
            let tags = this.tagArr.map((obj) => {
                return obj._id;
            });
            if(title === ''){
                this.$message({
                    type: 'warning',
                    message: '!'
                });
                return;
            }
            if(tags.length === 0){
                this.$message({
                    type: 'warning',
                    message: '!'
                });
                return;
            }
            if(content === ''){
                this.$message({
                    type: 'warning',
                    message: '!'
                });
                return;
            }
            let abstract;
            if(content.indexOf('<!--more-->') !== -1 && content.split('<!--more-->')[0] !== ''){
                abstract = content.split('<!--more-->')[0];
            }else{
                this.$message({
                    type: 'warning',
                    message: '!'
                });
                return;
            }
            //
            let article = {
                title,
                content,
                abstract,
                tags
            };
            this.$store.dispatch('saveArticle', article)
                .then(res => {
                    if(res){
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                })
                .catch(err => {
                    console.log('');
                    this.$message({
                        type: 'error',
                        message: '!'
                    });
                });
        },
        //enter(5)-----
        addTag(){
            if(this.tagArr.findIndex(v => v.name === this.articleTag ) > -1){
                this.$message({
                    type: 'warning',
                    message: '!'
                });
                return ;
            }
            if(this.tagArr.length >= 5){
                this.$message({
                    type: 'warning',
                    message: '5!'
                });
                return;
            }
             this.$store.dispatch('createTag', this.articleTag)
                .then(res => { //res{id: 'XXX', name: 'xxx'}
                    if(res){
                        this.tagArr.push(res);
                        this.articleTag = '';
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }
                })
                .catch(err => {
                    console.log('');
                    this.$message({
                        type: 'error',
                        message: '!'
                    });
                }); 
        },
        //tagArr
        deleteTag(id, index){
            this.$confirm(',?', '')
                .then(() => {
                    this.$store.dispatch('deleteTag', id)
                        .then(res => {
                            if(res){
                                this.tagArr.splice(index, 1);
                                this.$message({
                                    type: 'success',
                                    message: ''
                                });
                            }
                        })
                        .catch(err => {
                            console.log('');
                            this.$message({
                                type: 'error',
                                message: ''
                            });
                        });
                })
                .catch(() => {});
        },
    }
}
</script>

<style lang="stylus">
//
// markdown
// 
// code

@import '../../../assets/stylus/markdown.styl';
@import '../../../assets/stylus/preview.styl';
@import '../../../assets/stylus/code.styl';

.CodeMirror-fullscreen
        background-color: #fff
.CodeMirror, .CodeMirror-scroll
	    min-height: 400px
</style>

<style lang="stylus" scoped>
@import '../../../assets/stylus/_setting.styl';
.article-wrapper
    width: calc(100% + 20px)
    height: 100%
    overflow-x: hidden
    overflow-y: auto
    .article
        position: relative
        padding: 20px
        .title, .tag
            input 
                height: 25px
                width: 300px
                padding-left: 8px
                margin-bottom: 20px
                border: none
                border-bottom: 1px solid $green
                outline: none
                font-size: 18px
        .tag
            position: relative
            .tags-wrapper
                position: absolute
                left: 391px
                top: 1px
                white-space: nowrap
                overflow: hidden
                font-size: 0
                .has-tag
                    position: relative
                    display: inline-block
                    height: 30px
                    line-height: 30px
                    font-size: 14px
                    margin-right: 20px
                    cursor: pointer
                    .i-tag
                        font-size: 14px
                        padding-right: 5px
                    &:hover
                        &>i 
                            display: block
                    &>i 
                        position: absolute
                        top: -10px
                        right: -14px
                        display: none
                        color: $green
        .btn
            position: absolute
            top: 20px
            right: 20px
            cursor: pointer
            .delete, .save
                display: inline-block
                font-size: 16px
                width: 80px
                height: 30px
                margin-right: 10px
                line-height: 30px
                text-align: center
                border: 1px solid #00
                border-radius: 5px
            .save
                background-color: $green
                color: #fff
                border: 1px solid $green
</style>


