import marked from 'marked';

//highlight.js ,hljs
import './highlight.pack.js';
//
const languages = hljs.listLanguages()
//
hljs.configure({
  tabReplace: '    ', // 4 spacestab4
});

marked.setOptions({
  renderer: new marked.Renderer(), // 
  gfm: true, //githubmarkdown
  pedantic: false, // markdown.pl
  sanitize: false, //html
  tables: true, //
  breaks: true, //
  smartLists: true, //markdown pedantic.
  smartypants: true, //
  highlight: function(code, lang) {
    if(!lang){
      return hljs.highlightAuto(code).value;
    }
    if(languages.indexOf(lang) === -1){ //,
      return hljs.highlightAuto(code).value;
    }
    return hljs.highlight(lang, code).value; 
  }
});

//hljshttp://blog.csdn.net/spy19881201/article/details/38866033
//mdmarked.jshtml,htmlhighlight.jspre

//highlight
// highlightcodelangcallback
// code
// lang
// callback

export default marked;


